import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { AssociationRelation } from "./association-relation.component";

import { CustomTopologyRelationComponent } from "./custom-topology-relation/custom-topology-relation.component";
import { InternalStaffRelationsComponent } from "./internal-staff-relations/internal-staff-relations.component";


//页面

const routes: Routes = [
    {
        path: '',
        component: AssociationRelation,
        children: [
            {path: 'association-relation-manager', loadChildren: './association-relation-manager/association-relation-manager.module#AssociationRelationManagerModule'},              
            {path: 'internal-staff-relations', loadChildren: './internal-staff-relations/internal-staff-relations.module#InternalStaffRelationsModule'}, 
            {path: 'custom-topology-relation', component: CustomTopologyRelationComponent},
            {path:'custom-relationship-maintenance',loadChildren:'./custom-relationship-maintenance/custom-relationship-maintenance.module#CustomRelationshipMaintenanceModule'},
            {path: '', component: InternalStaffRelationsComponent},    
        ]
    }
];
export const AssociationRelationRouting: ModuleWithProviders = RouterModule.forChild(routes);
